<template>
	<div class="container">
		<div class="row min-vh-100 align-items-center justify-content-center">
			<div class="col-md-5">
				<div class="card">
					<div class="card-header bg-white text-center">
						<img :src="this.laravel.assets + 'img/brands/silta-alt-color.svg'" :style="{width:'50%'}" />
					</div>
					<div class="card-body">
						<h3 class="text-center">Selamat Datang!</h3>
						<p class="text-center text-muted">Masuk dahulu ya dengan kredensial anda.</p>

						<form :action="this.laravel.base_url + '/api/auth/login'" id="login" novalidate="novalidate" method="post">

							<div class="form-group mb-3">
								<label for="email">Email</label>
								<div class="input-group">
									<div class="input-group-prepend">
										<span class="input-group-text"><i class="fas fa-envelope fa-fw"></i></span>
									</div>
									<input type="text" class="form-control" id="email" name="email" placeholder="Email Anda" />
								</div>
							</div>

							<div class="form-group mb-3">
								<div class="d-flex justify-content-between">
									<label for="password">Kata Sandi</label>
									<router-link to="/lupa-sandi">Lupa Kata Sandi?</router-link>
								</div>
								<div class="input-group">
									<div class="input-group-prepend">
										<span class="input-group-text"><i class="fas fa-key fa-fw"></i></span>
									</div>
									<input type="password" required class="form-control" id="password" name="password" placeholder="Kata Sandi Anda" />
									<div class="input-group-append">
										<button type="button" @click="showPassword" class="btn btn-password btn-outline-primary"><i class="fas fa-eye"></i></button>
									</div>
								</div>
							</div>

						<div class="custom-control mb-3 custom-checkbox">
							<input type="checkbox" class="custom-control-input" id="remember" name="remember" />
							<label class="custom-control-label" for="remember">Ingatkan Saya</label>
						</div>

							<button class="btn btn-primary btn-block" type="submit"><i class="fas fa-sign-in-alt mr-1 fa-fw"></i>Masuk</button>
						</form>
					</div>
				</div>
				<Footer />
			</div>
		</div>
	</div>
</template>

<style lang="scss">
.svg-inline--fa.fa-w-18 {
	width: 1.25rem;
}
</style>

<script>
import Footer from './templates/Footer.vue';
import Router from '../router.js';

export default {
	components: {
		Footer
	},
	data() {
		return {
			laravel: window.laravel,

			username: null,
			password: null,
			remember: null,
		}
	},
	mounted: function() {
		document.title = 'Masuk Dahulu';

		// Define Custom Bootstrap
		const swalWithBootstrapButtons = Swal.mixin({
			customClass: {
				confirmButton: 'btn btn-success btn-block btn-lg',
				denyButton: 'btn btn-danger btn-block btn-lg',
				cancelButton: 'btn btn-muted btn-block btn-lg'
			},
			buttonsStyling: false,
		});

		$('#login').validate({
			rules: {
				email: {
					required: true,
					email: true,
					minlength: 3,
				},
				password: {
					required: true,
					minlength: 5,
				},
			},
		    errorElement: 'span',
		    errorPlacement: function (error, element) {
		        error.addClass('invalid-feedback');
		        element.closest('.form-group').append(error);
		    },
		    highlight: function (element, errorClass, validClass) {
		        $(element).addClass('is-invalid');
		        $(element).removeClass('is-valid');
		    },
		    unhighlight: function (element, errorClass, validClass) {
		        $(element).removeClass('is-invalid');
		        $(element).addClass('is-valid');
		    },
		    submitHandler: function(e) {

		    	// Lock any Input and Button
		    	$('input, button').prop({disabled: true});

		    	// Set SweetAlert Modal to Prevent Change
				swalWithBootstrapButtons.fire({
					title: 'Tunggu Sebentar',
					icon: 'info',
					html: 'Permintaan anda sedang diproses. Silahkan menunggu beberapa saat!',
					allowOutsideClick: false,
					showConfirmButton: false,
				});

				// Define Form
		    	const form = $("#login"),
		    		  res  = { email: $('#email').val(), password: $('#password').val(), remember: $('#remember').val() };

		    	// console.log(form, res);

		    	// Processing Data
		    	$.post(form.attr('action'), res)
		    	.done(function(response) { // If is Success
					setTimeout(function() {
						Swal.close();
						Router.push('/mengarahkan');
					}, 2500);
		    	})
		    	.fail(function(response) { // If Fail and Can't Catch Any Data
		    		// Unlock any Input and Button
		    		$('input, button').prop({disabled: false});
		    		let $alert = response.responseJSON;

					swalWithBootstrapButtons.fire({
						title: ($alert.title ? $alert.title : 'Maaf, ada gangguan!'),
						icon: ($alert.severity ? $alert.severity : 'error'),
						html: ($alert.html ? $alert.html : 'Maaf, ada gangguan pada peladen! Silahkan coba beberapa saat lagi!'),
						showConfirmButton: false,
						showDenyButton: false,
						showCancelButton: true,
						cancelButtonText: "Tutup",
					});
		    	});

		    },
		});
	},
	methods: {
		showPassword: function(e) {
			e.preventDefault();

			// Define Selected Elements
			var input = jQuery('.btn-password').parents('.form-group').find('#password'),
				self  = jQuery('.btn-password').find('svg');

			// For Changing Input Type
			if(input.attr('type') === 'password') {
				input.attr('type', 'text');
			} else {
				input.attr('type', 'password');
			}

			// For Changing Icon
			if(self.data('icon') === 'eye') {
				self.attr('data-icon', 'eye-slash');
			} else {
				self.attr('data-icon', 'eye');
			}
		}
	}
}
</script>
